@import url('https://fonts.googleapis.com/css2?family=Redressed&display=swap');

button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 30px;
    outline: none;
}

body {
    background-color: dimgray;
    color:gold;
}

#root {
    font-family: 'Redressed', cursive;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#wrapper {
    width: 400px;
    height: 400px;
    border: goldenrod 10px solid;
    border-radius: 50%;
    align-items: flex-start;
}

#title {
    margin-top: 30px;
    font-size: 50px;
}

div {
    width: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.time-control {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 25px;
}

#break-length, #session-length, #timer-label {
    font-size: 35px;
}

#time-left {
    font-size: 40px;
}

